<template>
    <div>
        <loader :is-visible="isLoading"></loader>
        <tg-list :IsShowAction="true" :showCheckBox="true" ModuleName="Timer" SubModuleCode="Timer"
            IdentityColumn="TagId" :ListData="TimerTagList" :HeaderData="Headers" 
            :HeaderButtons="Buttons" :TotalRecords="TotalRecords" :IsSearchFieldsOnTop="false"
            SearchPosition="top" :SearchFields="topsearchField" v-on:ActionButtonClick="actionButtonClick"
            :RenderRowActionMethod="renderActions" :ListDataCallBackFunction="GetTimerTagListing"  v-on:LeftsearchButtonClick="leftCommonSearch"
            :IsFilterApplied="searchCondition ? true : false" :LegendArray="[]"
            v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick" :NorecordfoundText="$t('NoRecordfound')">
            <template v-slot:slotdata="{data}">
                <template v-if="data.column.COLUMN_NAME == 'StatusId'">
                    <listingstatus :isfor="'Timer'" v-if="isCRMShowStausDrop(data.row.StatusId, 'Timer')"
                        ref="updatestatus"  v-bind:dataid="data.row.TagId" data-controller="Timer"
                        v-bind:statuslist="StatusListForListing" v-bind:item="data" data-action="index" module="Timer" subModule="Timer"
                        v-bind:dataselected="data.row.StatusId == '1001' ? 'Active' : 'Inactive'"
                        v-bind:callbackfunction="GetTimerTagListing" v-bind:isselectedvaluestring="true">
                    </listingstatus>
                </template>
            </template>
        </tg-list>
        <modalmanagetags v-if="showAddPopup" ref="EditManageTag" :EditTagsData="EditTagsData"></modalmanagetags>
    </div>
</template>

<script>
import DataService from '../../services/DataService.js';
import listingstatus from '../../components/Common/Listing.vue';
import modalmanagetags from '../AdvanceSetup/ModalManageTags.vue';
export default {
    components: {
        listingstatus,
        modalmanagetags
    },
    data() {
        return {
            isLoading:false,
            TimerTagList:[],
            PageNumber:1,
            PageSize:10,
            TotalRecords:0,
            TotalPages: 0,
            showAddPopup:false,
            EditTagsData:[],
            searchCondition:'',
            noRecord:true,
            Headers: [
                {
                    COLUMN_NAME: "TagName",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: this.$t('TagName'),
                    DISPLAY_ORDER: 1,
                }, {
                    COLUMN_NAME: "TagDesc",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: this.$t('TagDescription'),
                    DISPLAY_ORDER: 1,
                },
                {
                    COLUMN_NAME: "StatusId",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: this.$t('Status'),
                    DISPLAY_ORDER: 1,
                },
            ],
            topsearchField : [
                {
                    fieldName: 'Search',
                    fieldType: "text",
                    fieldSearchConditionName: ['TagName'],
                    isSearch: true,
                    value: "",
                    listOptions: []
                }
            ],
            StatusListForListing:[
                { 
                    STATUS: 'Active', 
                    STATUS_ID: 1001, 
                    STATUS_COLOR: "#ffffff", 
                    STATUS_BG_COLOR: "#28a745" 
                },{ 
                    STATUS: 'Inactive', 
                    STATUS_ID: 1002, 
                    STATUS_COLOR: "#ffffff", 
                    STATUS_BG_COLOR: "#d72435"
                }
            ],
            Buttons:[
                {
                    title: 'Add',
                    iconClass: 'fa-plus',
                    callbackfunction: 'AddScreenPopup',
                    additionalClass: '',
                    isdisabled: false,
                    isvisible: true,
                }, {
                    id: 'ActiveMultiple',
                    title: 'Active',
                    iconClass: 'fa-check',
                    callbackfunction: 'ActiveMultipleListing',
                    additionalClass: '',
                    isdisabled: true,
                    isvisible: true,
                }, {
                    id: 'InactiveMultiple',
                    title: 'Inactive',
                    iconClass: 'fa-close',
                    callbackfunction: 'InactiveMultipleListing',
                    additionalClass: '',
                    isdisabled: true,
                    isvisible: true,
                }, {
                    id: 'DeleteMultiple',
                    title: 'Delete',
                    iconClass: 'fa-trash-o',
                    callbackfunction: 'commonDeleteData',
                    isdisabled: true,
                    isvisible: true,
                }    
            ],
            listActions:[  
                {
                    title:'Edit',
                    href: 'javascript:void(0)',
                    callbackfunction: 'EditTags',
                    icon: 'fa action-icon fa fa-pencil',
                    additionalSpan:''
                },    
            ]     
        }

    },
    created: function () {
        this.GetTimerTagListing();
        ths.getStatuslistForListing("Timer", "en");
    },
    methods: {
        renderActions: function () {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'Edit':
                        action.isVisible = true;
                        break;
                    default:
                        action.isVisible = true;
                        break;
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        actionButtonClick: function (callbackfunction, item, index, event) {
            var vm = this;
            debugger
            switch (callbackfunction) {
                case 'EditTags':
                    vm.EditTags(item);
                    break;
            }
        },
        async GetTimerTagListing(){
            var vm = this;
            vm.isLoading = true;
            var params = `PageNumber=${vm.PageNumber}&PageSize=${vm.PageSize}&searchCondition=${vm.searchCondition}`;
            await DataService.GetTagListing(params).then(response=>{
                if(response.data.length > 0){
                    vm.Headers.forEach(column=>{
                        if(column.COLUMN_NAME == 'StatusId'){
                            column.settings = {
                                isInSlot: true
                            };
                        }
                    })
                    vm.TimerTagList = response.data;
                    vm.TotalRecords = response.data[0].Total_Record;
                    vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                    vm.noRecord = false;
                }else{
                    vm.TimerTagList = [];
                    vm.TotalRecords = 0;
                    vm.TotalPages = 0
                    vm.noRecord = true;
                }
                vm.isLoading = false;
            }).catch(err=>{
                console.log(err);
            })
        },
        TruncateStr: function (str, len) {
            var convertstring = '' + str;
            if (convertstring != null)
                if (convertstring.length <= len) return str;
                else return convertstring.substring(0, len) + "...";
        },
        pagerMethod: async function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            await this.GetTimerTagListing();
        },
        EditTags(data){
            var vm = this;
            vm.showAddPopup = true;
            vm.EditTagsData = data;
        },
        getStatuslistForListing: function (StatusType, lang) {
            var vm = this;
            var url ="langCode=" + lang + "&statusType=" + StatusType + "&hasglobal=true";
            DataService.statusdropdown(url).then(function (response) {
                if (response.data.status == "Success") {
                    var json = response.data.result;
                    vm.StatusListForListing = json.data;
                }
            });
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "commonDeleteData":
                    this.commonDeleteData();
                    break;

                case "ActiveMultipleListing":
                    this.UpdateStatus("Active");
                    break;

                case "InactiveMultipleListing":
                    this.UpdateStatus("Inactive");
                    break;

                case "AddScreenPopup":
                    debugger
                    this.showAddPopup = true
                    break;
            }
        },
        CloseTags(){
            var vm = this;
            vm.EditTagsData = [];
            vm.showAddPopup = false;
        },
        leftCommonSearch: async function (value) {
            this.PageNumber = value.PageNumber;
            // this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchCondition;
            }
            else {
                this.searchCondition = "";
            }

            await this.GetTimerTagListing();
        },
    },
}
</script>